.Input {
	width: 100%;
	padding: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.Label {
	font-weight: bold;
	display: block;
	margin-bottom: 8px;
}

.InputElement {
	outline: none;
	border: 1px solid #ccc;
	background-color: white;
	font: inherit;
	padding: 6px 10px;
	display: block;
	width: 100%;
}

.Invalid {
	border: 1px solid red;
	background-color: orange;
} 

.InputElement:focus {
	outline: none;
	background-color: #ccc;
}

.ValidationError {
	color: salmon;
	margin: 5px 0;
}


